<template>
    <div class="login">
      <el-form :model="formData" status-icon  label-width="100px" class="demo-ruleForm">
        <el-form-item label="用户名" prop="userName">
          <el-input type="password" v-model="formData.userName"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pwd">
          <el-input type="password" v-model="formData.pwd" ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>
<script>
    export default{
      name: "",
      data(){
          return{
              formData:{
                userName:'',
                pwd:""
              }
          }
      },
      methods:{
          submit(){
              this.$axios.post('login',this.formData).then( res => {
                  if(res.code == 200){
//                    let token = "Bearer "+res.data;
                    let token = res.data;
                    //将获取的签证存放到localStorage
//                    localStorage.setItem("authorization",token)  //将获取的签证以键值对的形式存放到localStorage
                    localStorage.setItem("token",token)  //将获取的签证以键值对的形式存放到localStorage
                    this.$message({
                      message:`${res.msg}`,
                      type:'success'
                    })
                    setTimeout( () => {
                      this.$router.push('/')
                    },1000)
                  }else {
                    this.$message({
                      message:`${res.msg}`,
                      type:'warning'
                    })
                  }
              })
          }
      }
    }
</script>
<style scope>
.login {
  margin: 100px auto;
}
</style>



